  width: 300px;
  border-color: #004d9f;
  outline: none;
  padding: 10px;
  font-size: 16px;
}

.button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 20%;
  padding: 10px 20px;
